.skill_card{
    width: max-content;
    padding: 8px 16px;
    height: 120x;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 6px 0px 6px 6px;
    border-radius: 8px;
    /* background-color: aquamarine; */
}

.skill_icon{
    font-size: 80px;
}

.skill_name{
    font-size: 18px;
}

@media only screen and (max-width:720px){
    .skill_card{
        width: max-content;
        height: 100px;
    }
    .skill_icon{
        font-size: 60px;
    }
}