.header{
    display: flex;
    /* background-color: aqua; */
    justify-content: space-between;
    flex: 1;
    padding: 12px 12px;
    position: relative;
    align-items: center;
}

.menu{
    /* background-color: bisque; */
    display: flex;
    flex: 1;
    justify-content: flex-end;
}

.menu_icon{
    font-size: 24px;
}

.mobile_menu{
    display: none;
}

.web_menu{
    display: flex;
}

.logo{
    display: flex;
    font-size: 26px;
    color: blueviolet;
    font-weight: 800;
}
.logo:hover{
    color: black;
}

@media only screen and (max-width:720px){
    .mobile_menu{
        display: flex;
    }
    .web_menu{
        display: none;
    }
    .logo{
        font-size: 20px;
    }
}