.contact_div{
    width: 80%;
    border-radius: 10px;
    border:1px solid rgb(92, 92, 221);
}

.inputcss{
    border:1px solid rgb(92, 92, 221);
    margin: 5px 0px;
}

.btncss{
    background-color: rgb(92, 92, 221);
    color: rgb(255, 211, 98);
}
.btncss:hover{
    color: white;
}



@media only screen and (max-width:720px){
    .contact_div{
        width: 100%;
    }
}
