.mobile{
    background-color: white;
    height: 100vh;
    width: 60vw;
    position: absolute;
    right: 0px;
    top: -6px;
    background-color: rgb(166 202 255);
    padding: 18px 12px;
    display: flex;
    flex-direction: column;
}

.close_icon{
    display: flex;
    justify-content: flex-end;
    font-size: 24px;
}

.mobile_options{
    display: flex;
    flex-direction: column;
    width: 100%;
    /* background-color: yellowgreen; */
    padding-top: 25px;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.mobile_option{
    font-size: 24px;
    margin: 12px 0px;
    display: flex;
}

.mobile_option a{
    text-decoration: none;
    color: black;
    display: flex;
}

.mobile_option a:hover{
    color: blueviolet;
}