.about{
    padding-bottom: 48px;
}

.about_top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.picture{
    height: 420px;
}

.about_info{
    font-size: 24px;
}

.info_name{
    color: blue;
    font-size: 32px;
}
.info_name:hover{
    color: blueviolet;
}

@media only screen and (max-width:1320px) {
    .about_info{
        font-size: 20px;
    }
}
@media only screen and (max-width:891px) {
    .about_top{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .picture{
        height: 280px;
    }
    .about_info{
        font-size: 20px;
    }
    .info_name{
        font-size: 26px;
    }
}