.project_card{
    flex: 1;
    display: flex;
    justify-content: space-between;
    margin: 16px 0px;
    padding: 8px 0px;
}
.project_photo{
    width: 40%;
    /* display: flex;
    justify-content: center;
    align-items: center; */
}

.project_info{
    margin-right: 24px;
}

.project_title{
    font-size: 20px;
    font-weight: 600;
}

.project_tags{
    display: flex;
    max-width: 60%;
    flex-wrap: wrap;
}

.tag{
    padding: 4px 8px;
    background-color: aquamarine;
    border-radius: 4px;
    margin-right: 6px;
    margin-bottom: 4px;
}

.project_links{
    margin-top: 6px;
    display: flex;
}

.project_links a{
text-decoration: none;
color: black;
}



.link_button{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: aquamarine;
    width: max-content;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 16px;
    margin-right: 6px;
}

.link_button i{
    display: flex;
    align-items: center;
    margin-right: 4px;
}

@media only screen and (max-width:720px) {
    .project_photo{
        width: 70%;
    }
    .project_card{
        flex-direction: column-reverse;
        /* justify-content: center; */
        align-items: center;
    }
    .project_info{
        margin-left: 0px;
        margin-top: 12px;
    }
    .project_tags{
        max-width: 100%;
    }
    /* .project_about{
        font-size: 12px;
    } */
}