.web{
    display: flex;
    flex: 1;
    align-items: center;
}

.web_option{
    font-size: 20px;
    margin-right: 15px;
    font-weight: 700;
}

.web_option a{
    text-decoration: none;
    color: black;
    display: flex;
    align-items: center;
}

.web_option a:hover{
    color: blueviolet;
}

.option_icon{
    display: flex;
    align-items: center;
    margin-right: 5px;
}

.web_option:last-child{
    margin-right: 0px;
}